import React, { Component } from "react";
import { connect } from "react-redux";
import FormPembayaranPenjualanLM from "./Components/FormPembayaranPenjualanLM";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import Stepper from "react-stepper-horizontal/lib/Stepper";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
class PembayaranPenjualanLM extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      step: 0,
      step1: "row",
      step2: "row d-none",
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "namaToko",
          text: "Nama Toko",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "sales",
          text: "Sales",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "bonRp",
          text: "Bon (Rp)",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "bonGr",
          text: "Bon (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
      ],
      data: [
        {
          tanggal: "2020-11-10",
        },
      ],
    };
  }

  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    } else if (step === 1) {
      this.setState({ step: step + 1, step2: "row d-none", step3: "row" });
    }
  };

  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    } else if (step === 2) {
      this.setState({ step: step - 1, step3: "row d-none", step2: "row" });
    }
  };

  render() {
    return (
      <>
        <div className="container-fluid">
          <div className="card">
            <div className="card-body">
              <div className="col-lg-12">
                <h2>Pembayaran Penjualan LM</h2>
              </div>
              <br />
              <div className="col-lg-12 col-md-12">
                <Stepper
                  steps={[{ title: "Pilih Data" }, { title: "Konfirmasi" }]}
                  activeStep={this.state.step}
                />
              </div>
              <div className="col-lg-12">
                <div className={this.state.step1}>
                  <div className="col-lg-12">
                    <FormPembayaranPenjualanLM />
                  </div>
                  <div className="col-lg-12">
                    <ToolkitProvider
                      keyField="kodeKelompok"
                      data={this.state.data}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <div className="text-right mt-5 mb-3">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Tuliskan Kode / Nama"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                        </div>
                      )}
                    </ToolkitProvider>
                  </div>
                  <div className="col-lg-12">
                    <div className="text-right">
                      <button
                        className="btn btn-primary"
                        onClick={() => this.nextStep()}
                      >
                        Next
                      </button>
                    </div>
                  </div>
                </div>
                <div className={this.state.step2}>
                  <ToolkitProvider
                    keyField="kodeKelompok"
                    data={this.state.data}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="form-group">
                        <div className="text-right mt-5 mb-3">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Tuliskan Kode / Nama"
                            style={{ margin: 0, top: "50%" }}
                          />
                        </div>
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          pagination={paginationFactory()}
                        />
                        <ExportCSVButton
                          {...props.csvProps}
                          className="btn btn-primary"
                        >
                          Export CSV
                        </ExportCSVButton>
                      </div>
                    )}
                  </ToolkitProvider>
                  <div className="col-lg-12">
                    <div className="row">
                      <div className="col-lg-6">
                        <div className="text-left">
                          <button
                            className="btn btn-primary"
                            onClick={() => this.prevStep()}
                            type="button"
                          >
                            Back
                          </button>
                        </div>
                      </div>
                      <div className="col-lg-6">
                        <div className="text-right">
                          <button className="btn btn-primary" type="submit">
                            Simpan
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default connect()(PembayaranPenjualanLM);
